import React from 'react';
import cx from 'classnames';
import shallowCompare from 'react/lib/shallowCompare';
// import shallowEqual from 'fbjs/lib/shallowEqual';

import s from './profile.css';


require('../../fontello/css/fontello.scss'); // for logout icon

class Profile extends React.Component {

  static propTypes = {
    className: React.PropTypes.string,
    style: React.PropTypes.object,
    onClick: React.PropTypes.func,
    user: React.PropTypes.object.isRequired,
  };

  constructor(props) {
    super(props);
    this.state = {};
  }


  componentDidMount() {
  }

  componentWillReceiveProps() {
  }
  shouldComponentUpdate(nextProps, nextState) {
    return shallowCompare(this, nextProps, nextState);
  }
  onClick() {
    if (this.props.onClick) {
      this.props.onClick(this.props.user);
    }
  }

  // fetchGithub(props) {
  //     const providerData = props.user.providerData && props.user.providerData.length
  //         ? props.user.providerData[0]
  //         : null;
  //     // console.log(providerData);
  //     // console.log(/github.com/.test(providerData[0].providerId))
  //     if (providerData && /github.com/.test(providerData.providerId)) {
  //         window.fetch(`http://api.github.com/user/${providerData.uid}`).then((response) => {
  //             this.setState({
  //                 displayName: response.json().login
  //             })
  //         },(err)=>{console.log(err) })
  //     } else {
  //         this.setState({
  //             displayName: undefined
  //         })
  //     }
  // }

  render() {
    const user = this.props.user;
    const providerData = user.providerData && user.providerData.length ? user.providerData[0] : null;

    const displayName = this.state.displayName || providerData.displayName || user.displayName;
    const email = providerData.email || user.email;
    const photoURL = providerData.photoURL || user.photoURL;
    const uid = providerData.uid;


    return (
      <div className={cx(s.profile, this.props.className)} style={{ ...this.props.style }}>
        {
          user.photoURL ? <img role="presentation" src={photoURL} className={s.photo} /> : undefined
        }
        <div className={s.content}>
          <span className={s.name}>{`${displayName || email || uid}`}</span>
          {
            // <span className={s.email}>{`${email || 'emaiasdasdaasd@gamil.com'}`}</span>
          }
        </div>
        <i
          className={cx('icon-logout ', s.logoutLogo)}
          onClick={this.onClick.bind(this)}
        />

      </div>
    );
  }

}

//  <i className={cx("icon-logout ", s.loginLogo) }></i>
//                 <i className={cx("icon-login-1 ", s.loginLogo) }></i>
//                 <i className={cx("icon-logout-1 ", s.loginLogo) }></i>

module.exports = Profile;



