//  that how config looks

//     const config = {
//         apiKey: "apiKey",
//         authDomain: "projectId.firebaseapp.com",
//         databaseURL: "https://databaseName.firebaseio.com",
//         storageBucket: "bucket.appspot.com",

//     };

const firebase = require('firebase/app');
require('firebase/auth');


const defaultProviders = {
  google: {
    provider: null,
    init() {
      this.provider = new firebase.auth.GoogleAuthProvider();
      this.provider.addScope('https://www.googleapis.com/auth/plus.login');
    },
  },
  facebook: {
    provider: null,
    init() {
      this.provider = new firebase.auth.FacebookAuthProvider();
      this.provider.addScope('user_birthday');
    },
  },

  twitter: {
    provider: null,
    init() {
      this.provider = new firebase.auth.TwitterAuthProvider();
    },
  },
  github: {
    provider: null,
    init() {
      this.provider = new firebase.auth.GithubAuthProvider();
      this.provider.addScope('repo');
    },
  },
};


/* this class simplifies named app creation with firebase */

class FirebaseApp {

  constructor({ providers, name }) {
    this.appName = name || '[DEFAULT]';
    this.providers = providers || defaultProviders;
    this.currentAuthentication = null;
    if (!this.providers) {
      throw new Error('FirebaseApi requires providers object. See contructor of firebase-api');
    }
  }

  logError(error) {
    console.error(error);
  }

  validateConfig() {
    return Promise.resolve().then(() =>
      firebase.app(this.appName).auth().signInWithPopup(new firebase.auth.GoogleAuthProvider())
    ).catch((err) => {
      throw err;
    });
  }

  app() {
    return firebase.app(this.appName);
  }


  deleteApp() {
    return Promise.resolve().then(() =>
      Promise.resolve().then(() => {
        firebase.app(this.appName).delete();
      }).catch((/* err*/) => { /* empty */ })
    );
  }

  initApp(config) {
    return Promise.resolve().then(() => {
      firebase.initializeApp(config, this.appName);
    });
  }


  signIn(provider) {
    // TODO
    // fetch needed data for github and other providers "https://api.github.com/user/19359748"
    return Promise.resolve().then(() => {
      if (!this.providers[provider].provider) {
        this.providers[provider].init();
      }
      return firebase
        .app(this.appName)
        .auth()
        .signInWithPopup(this.providers[provider].provider);
    }).then((result) => {
      // This gives you a Google Access Token. You can use it to access the Google API.
      // const token = result.credential.accessToken;

      // The signed-in user info.
      // const user = result.user;

      // console.log(user);
      // ...
      this.currentAuthentication = result;
      return result;
    }).catch((firebaseError) => {
      // Handle Errors here.
      // const errorCode = firebaseError.code;
      // const errorMessage = firebaseError.message;

      // The email of the user's account used.
      // const email = firebaseError.email;

      // The firebase.auth.AuthCredential type that was used.
      // const credential = firebaseError.credential;
      // ...

      /* thwo is important to keep error promise chain working in 'endpoint' function */
      throw firebaseError;
    });
  }

  signOut(/* user*/) {
    return Promise.resolve().then(() =>
      Promise.resolve()

        .then(() =>
          firebase
            .app(this.appName)
            .auth()
            .signOut()
        )
        .then(() => this.currentAuthentication)
    );
  }

}

module.exports = ({ providers, name }) => new FirebaseApp({ providers, name });



