
import React from 'react';

import flexbox from '../css/flexbox.css';
import s from './button.css';
import cx from 'classnames';

require('../css/colors.scss');

class Button extends React.Component {

    static propTypes = {
        text: React.PropTypes.string,
        icon: React.PropTypes.string,
        colorName: React.PropTypes.string,
        className: React.PropTypes.string,
        onClick: React.PropTypes.func,
        pressed: React.PropTypes.bool
    };

    constructor(props) {
        super(props);
        this.state = {
            pressed: props.pressed || false
        };
    }

    onClick(evt) {
        evt.persist();

        if (evt.nativeEvent.button === 0) {
            this.toggle('pressed', () => {
                if (this.props.onClick) {
                    this.props.onClick(evt);
                }
            });
        }
    }
    toggle(prop, callback) {
        this.setState({
            [prop]: !this.state[prop]
        }, callback);
    }
    componentDidMount() {
        // console.log("did mount")
    }
    componentWillReceiveProps(props) {
        if (typeof props.pressed !== 'undefined') {
            this.setState({
                pressed: props.pressed
            })
        }

    }
    componentDidUpdate() {
        // this.setState({ opacity: 1 })
    }

    render() {
        return (
            <button
                className={
                    cx('flexbox row nowrap align-items-center',
                        s.button,
                        `color-${this.props.colorName}${this.state.pressed ? '-lightened' : ''}`)
                }
                onClick={this.onClick.bind(this) } >
                {  this.state.pressed
                    ? <i className={cx('', `fa-${'flag'}`) }></i>
                    : undefined // `fa fa-fw fa-${'flag'}`
                }
                <span className={cx('', s.text) } > { this.props.text }</span>
            </button>
        );
    }

}

export default Button;



