import React from 'react';
import shallowCompare from 'react/lib/shallowCompare';
import shallowEqual from 'fbjs/lib/shallowEqual';
import cx from 'classnames';


import LoginPanel from './login-panel';
import Profile from './profile';

import s from './index.css';


const fireapp = require('./firebase-app')({ name: 'FirebaseLogins' });

class FirebaseLogins extends React.Component {

  static propTypes = {
    config: React.PropTypes.object.isRequired,
    className: React.PropTypes.string,
    style: React.PropTypes.object,
    google: React.PropTypes.bool,
    facebook: React.PropTypes.bool,
    twitter: React.PropTypes.bool,
    gihub: React.PropTypes.bool,
    onError: React.PropTypes.func,
    onSignInOut: React.PropTypes.func,
  };

  constructor(props) {
    super(props);
    this.state = {
      user: undefined,
      error: null,
    };
  }
  componentDidMount() {
    this.initApp(this.props.config);
  }
  componentWillReceiveProps(props) {
    this.initApp(props.config);
  }
  shouldComponentUpdate(nextProps, nextState) {
    return shallowCompare(this, nextProps, nextState);
  }

  onSignIn(provider) {
    Promise.resolve()
      .then(() => this.signIn(provider))
      .then((result) => {
        if (this.props.onSignInOut) {
          this.props.onSignInOut(result);
        }
      })
      .catch((error) => this.handleError(error));
  }
  onSignOut(user) {
    Promise.resolve()
      .then(() => this.signOut(user))
      .then((result) => {
        if (this.props.onSignInOut) {
          this.props.onSignInOut(result);
        }
      })
      .catch((error) => this.handleError(error));
  }
  signIn(provider) {
    // TODO
    // fetch needed data for github and other providers "https://api.github.com/user/19359748"
    return fireapp.signIn(provider).then((result) => {
      // This gives you a Google Access Token. You can use it to access the Google API.
      // const token = result.credential.accessToken;

      // The signed-in user info.
      const user = result.user;

      this.setState({
        user,
      });
      return result;

      // console.log(user);
      // ...
    });
  }

  signOut(user) {
    return Promise.resolve().then(() => {
      this.setState({
        user: undefined,
      });
      console.info(`user ${user.displayName} signed out`);
      return fireapp.signOut();
    });
  }

  handleError(error) {
    if (this.props.onError) {
      this.props.onError(error);
    }
    console.info('Error from handleError() of FirebaseLogins component:');
    console.error(error);
    this.setState({
      error,
    });
  }

  initApp(config) {
    Promise.resolve()
      .then(() => fireapp.app())
      .then((app) => {
        if (!shallowEqual(app.options, config)) {
          throw new Error('initApp');
        }
        this.setState({
          user: app.auth().currentUser,
        });
      })
      .catch((/* initApp*/) => {
        Promise.resolve()
          .then(() => fireapp.deleteApp())
          .then(() => fireapp.initApp(config))
          .catch((error) => this.handleError(error));
      });
  }


  render() {
    const providerChosen = Object.keys(this.props).reduce((p, prop) => {
      if (fireapp.providers[prop]) {
        p.push(prop);
      }
      return p;
    }, []);

    return (
      <section
        className={cx(s.root, this.props.className)}
        style={{ ...this.props.style }}
      >
        { !this.state.user
          ? <LoginPanel
            providers={fireapp.providers}
            providersChosen={providerChosen}
            onClick={this.onSignIn.bind(this)}
          />
          : <Profile user={this.state.user} onClick={this.onSignOut.bind(this)} />
        }
      </section>
    );
  }

}

module.exports = FirebaseLogins;



