import React from 'react';
import cx from 'classnames';


import GoogleSvg from '../google-svg';
import FacebookSvg from '../facebook-svg';
import TwitterSvg from '../twitter-svg';
import GithubSvg from '../github-svg';

import s from './login-panel.css';


const svgs = {
  google: <GoogleSvg className={s.svg} />,
  facebook: <FacebookSvg className={s.svg} />,
  twitter: <TwitterSvg className={s.svg} />,
  github: <GithubSvg className={s.svg} />,
};

require('../../fontello/css/fontello.scss');

class LoginPanel extends React.Component {

  static propTypes = {
    className: React.PropTypes.string,
    style: React.PropTypes.object,
    google: React.PropTypes.bool,
    facebook: React.PropTypes.bool,
    twitter: React.PropTypes.bool,
    gihub: React.PropTypes.bool,
    providers: React.PropTypes.object.isRequired,
    providersChosen: React.PropTypes.array.isRequired,
    onClick: React.PropTypes.func,
  };

  constructor(props) {
    super(props);
    this.state = {};
  }

  onClick(provider) {
    if (this.props.onClick) {
      this.props.onClick(provider);
    }
  }

  render() {
    return (
      <div className={cx(s.root, this.props.className)} style={{ ...this.props.style }}>
        <span className={cx('', s.title)}>
          <i className={cx('icon-login ', s.loginLogo)} />
          <span>with</span>
        </span>
        <div className={s.providers}>
          {
            this.props.providersChosen.map((provider) =>
              <provider
                key={provider}
                className={cx('', s.provider)}
                onClick={this.onClick.bind(this, provider)}
              >
                {svgs[provider]}
              </provider>
            )
          }
        </div>
      </div>
    );
  }

}

//  <i className={cx("icon-logout ", s.loginLogo) }></i>
//                 <i className={cx("icon-login-1 ", s.loginLogo) }></i>
//                 <i className={cx("icon-logout-1 ", s.loginLogo) }></i>

module.exports = LoginPanel;



