
import React from 'react';
require('../css/flexbox.css');
import s from './tweet.css';
import cx from 'classnames';


class Tweet extends React.Component {

    constructor(props) {
        super(props);
        this.state = {};
    }

    componentDidMount() {

    }


    render() {
        const tweet = this.props.tweet;
        // return (
        //     <blockquote className={cx(s.twittertweet) }>
        //         <img style={{ maxWidth: "48px", maxHeight: "48px" }} src={tweet.user.profile_image_url}></img>
        //         <p>{tweet.text}</p>
        //         - {tweet.user.name} ( @{tweet.user.screen_name})
        //     </blockquote>
        // );
        const textRaw = tweet.retweeted_status ? tweet.retweeted_status.text : tweet.text;
        const text = textRaw.split('http')[0];

        return (
            <div className={cx('flexbox row nowrap', s.root) }>
                <div  className={cx('flexbox align-items-center', s.logo) }>
                    <img  src={tweet.user.profile_image_url} />
                </div>
                <div className={cx('flexbox column nowrap', s.content) } >
                    <header
                        className={cx('flexbox row wrap align-items-center', s.header) }>
                        <strong className={s.fullname}>{tweet.user.name}</strong>
                        <a target="_blank" href={`https://twitter.com/${tweet.user.screen_name}`}>
                            <span className={s.username}> @<b>{tweet.user.screen_name}</b></span>
                        </a>
                        <a
                            target="_blank"
                            href={`https://twitter.com/${tweet.user.screen_name}/status/${tweet.id_str}`}>
                            <span className={s.time}>{new Date(Number(tweet.timestamp_ms)).toLocaleTimeString() }</span>
                        </a>
                        <a
                            title={'see on Twitter'}
                            target="_blank"
                            href={`https://twitter.com/${tweet.user.screen_name}/status/${tweet.id_str}`}
                            >
                            <i className={cx('fa fa-twitter-square', s.twitterSquare) }></i>
                        </a>
                    </header>
                    {
                        tweet.retweeted_status
                            ? <div>
                                <i className={cx('fa fa-retweet', s.retweet) }></i>
                                <a target="_blank" href={`https://twitter.com/${tweet.retweeted_status.user.screen_name}`}>
                                    <small className={s.username}> @{tweet.retweeted_status.user.screen_name}</small>
                                </a>
                            </div>
                            : undefined
                    }
                    <section className={cx('flexbox column wrap', s.section) }>
                        <span  className={cx("flexbox row wrap", s.text) }>{text}</span>
                    </section>
                    <footer className={cx('flexbox column wrap', s.footer) }>
                        <div className={cx('flexbox row wrap', s.links) }>
                            {
                                tweet.entities.urls.map((e, i) => {
                                    return <a
                                        key={i}
                                        className={cx("flexbox", s.url) }
                                        target="_blank"
                                        href={e.expanded_url}
                                        ><span >{e.display_url}</span></a>
                                })
                            }
                        </div>
                    </footer>
                </div>
            </div>
        );
    }
}

export default Tweet;



