
import React from 'react';

import flexbox from '../css/flexbox.css';
import FlipMove from 'react-flip-move';
import Tweet from '../tweet';
import s from './flow.css';
import cx from 'classnames';

class Flow extends React.Component {

    static propTypes = {
        tweets: React.PropTypes.array.isRequired,
        className: React.PropTypes.string
    };

    constructor(props) {
        super(props);
        this.state = {};
    }

    componentDidMount() {
        // console.log("did mount")
    }
    componentWillReceiveProps() {
        // this.setState({ opacity: 0 })
    }
    componentDidUpdate() {
        // this.setState({ opacity: 1 })
    }


    render() {
        const tweets = this.state.tweets || this.props.tweets || [];
        return (
            <div  className={cx("flexbox column nowrap", s.root) } >
                <FlipMove duration={1000} enterLeaveAnimation="elevator" typeName="ul"  >
                    {
                        tweets.map((el, i, a) => {
                            return (
                                <Tweet  key={`${el.timestamp_ms}${el.id_str}`} tweet={el}/>
                            );
                        })
                    }
                </FlipMove>
            </div>
        );
    }

}

export default Flow;



