
import React from 'react';
import s from './index.css';

require('adven-ui/css/flexbox.css');
import VelocityComponent from 'velocity-react/velocity-component';
require('velocity-animate/velocity.ui');

import cx from 'classnames';

import tools from './tools-24220_960_720.png';

const animations = ['flash', 'shake', 'bounce', 'pulse', 'swing', 'tada'];

class UnderConstruction extends React.Component {

    constructor(props) {
        super(props);
        this.tid = 0;
        this.state = {
            animation: 'callout.tada'
        }
    }

    componentDidMount() {
        // window.componentHandler.upgradeElement(this.root);
        this.transform(1000);
    }

    componentWillUnmount() {
        clearTimeout(this.tid);
        // window.componentHandler.downgradeElements(this.root);
    }
    runAnimation(evt) {
        clearTimeout(this.tid);
        // this.tid = setTimeout(() => {
        //     this.setState({ animation: this.getAnimtion() }, () => {
        //         //this.refs.velocity.runAnimation({ stop: true });
        //     });
        // }, 510)
        this.refs.velocity.runAnimation({ stop: true });

    }
    getAnimtion() {
        return `callout.${animations[Math.round(Math.random() * 5)]}`;
    }
    transform(time) {
        clearTimeout(this.tid);
        return new Promise((resolve, reject) => {

            this.tid = setTimeout(() => {
                clearTimeout(this.tid);
                this.refs.transform.style.transition += 'transform 1s';
                this.refs.transform.style.transform += 'rotateX(360deg)';
                resolve();
            }, time)
        }).then((r) => {
            this.tid = setTimeout(() => {
                // console.log(this);
                clearTimeout(this.tid);
                this.refs.transform.style.transition = this.refs.transform.style.transition.replace(/transform 1s/g, '');
                this.refs.transform.style.transform = this.refs.transform.style.transform.replace(/rotateX(360deg)/g, '');
            }, 1000)
        }).catch((e) => { });;

    }
    render() {
        //animation="callout.bounce"
        return (
            <div className={cx("flexbox row wrap align items center", s.root) }>
                <img className={ s.img} src={tools} />
                <div  ref={'transform'}  className={cx("flexbox") }>
                    <VelocityComponent animation={this.state.animation || 'callout.bounce'} duration={500} ref={"velocity"} >
                        <span className={cx(s.message) }  ref={'message'} onClick={this.runAnimation.bind(this) }>The website is currently under construction</span>
                    </VelocityComponent>
                </div>
                <img className={ s.img} src={tools} />
            </div>
        );
        // return (
        //     <div className={cx("flexbox row nowrap align items center", s.root) }>
        //         <span className={cx(s.message) } ref={'message'} onClick={this.transform.bind(this) }>The website is currently under construction</span>
        //     </div>
        // );
    }
}

export default UnderConstruction;



